%blackbtn {
    font-weight: 600;
    background-color: #222222;
}

.row {
    display: flex;
    width: 100%;
    height: 40px;
    margin-bottom: 8px;
}

.btn {
    display: block;
    width: 40px;
    height: 40px;
    background-color: #444444;
    color: #ffffff;
    border-radius: 3px;
    margin-left: 6px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.key-backspase {
    width: 100px;
    @extend %blackbtn;
}
.key-tab {
    width: 50px;
    @extend %blackbtn;
}

.key-del {
    width: 44px;
    @extend %blackbtn;
}

.key-capsLock {
    width: 100px;
    @extend %blackbtn;
}

.key-enter {
    width: 86px;
    @extend %blackbtn;
}
.key-shift-left {
    width: 100px;
}
.key-shift-right {
    width: 86px;
}
